<template>
<v-app>
  <v-btn
    color="primary"
    fab
    small
    dark
    v-if="!isPlaying"
    v-on:click='play'
  >
    <v-icon>mdi-play</v-icon>
  </v-btn>
  <v-btn
    color="primary"
    fab
    small
    dark
    v-else
    v-on:click='pause'
  >
    <v-icon>mdi-pause</v-icon>
  </v-btn>
</v-app>
</template>

<script>
export default {
  data: () => {
    return {
      beggin: require("@/assets/music/beggin.mp3"),
      isPlaying: false,
      player: new Audio()
    };
  },
  methods: {
    play() {
      this.player.play();
      this.player.addEventListener(
        "ended",
        function() {
          this.play();
        }.bind(this)
      );
      this.isPlaying = true;
    },
    pause() {
      this.player.pause();
      this.isPlaying = false;
    }
  },
  created() {
    this.player.src = this.beggin;
  }
};
</script>